/*TYPOGRAPHY*/
/* importing mixing and variables */
html {
  font-size: 62.5%;
}
/* Rules For Body Contents */
body {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-size: 1.4rem;
  line-height: 1.5;
  font-weight: 400;
  color: #3a3b3c;
}
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: 'Roboto', sans-serif;
  color: #0a1825;
  font-weight: 700;
}
h1 span,
h2 span,
h3 span,
h4 span,
h5 span,
h6 span,
.h1 span,
.h2 span,
.h3 span,
.h4 span,
.h5 span,
.h6 span {
  color: #fee53c;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a,
.h1 a,
.h2 a,
.h3 a,
.h4 a,
.h5 a,
.h6 a {
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
}
h1,
.h1 {
  font-size: 48px;
  font-size: 4.8rem;
  line-height: 1.5;
  font-weight: 700;
}
@media screen and (min-width: 320px) and (max-width: 767px) {
  h1,
  .h1 {
    font-size: 25px;
    font-size: 2.5rem;
    line-height: 1.5;
  }
}
@media screen and (min-width: 768px) {
  h1,
  .h1 {
    font-size: 32px;
    font-size: 3.2rem;
    line-height: 1.5;
  }
}
@media screen and (min-width: 992px) {
  h1,
  .h1 {
    font-size: 48px;
    font-size: 4.8rem;
    line-height: 1.5;
  }
}
h2,
.h2 {
  font-size: 36px;
  font-size: 3.6rem;
  line-height: 1.5;
}
@media screen and (min-width: 320px) and (max-width: 767px) {
  h2,
  .h2 {
    font-size: 26px;
    font-size: 2.6rem;
    line-height: 1.5;
  }
}
@media screen and (min-width: 768px) {
  h2,
  .h2 {
    font-size: 32px;
    font-size: 3.2rem;
    line-height: 1.5;
  }
}
@media screen and (min-width: 992px) {
  h2,
  .h2 {
    font-size: 36px;
    font-size: 3.6rem;
    line-height: 1.5;
  }
}
h3,
