  font-size: 2.4rem;
  line-height: 1.5;
  font-weight: 700;
}
@media screen and (min-width: 320px) and (max-width: 767px) {
  h4,
  .h4 {
    font-size: 20px;
    font-size: 2rem;
    line-height: 1.5;
  }
}
@media screen and (min-width: 768px) {
  h4,
  .h4 {
    font-size: 22px;
    font-size: 2.2rem;
    line-height: 1.5;
  }
}
@media screen and (min-width: 992px) {
