#banner-area {
  background-image: url(../img/banner-bg.jpg);
  background-size: cover;
  background-position: center center;
  color: #fff;
  position: relative;
  padding-top: 235px;
  z-index: 1;
}
#banner-area:after {
  background: #12c9fe;
  background: -moz-linear-gradient(90deg, #12c9fe 0%, #0e70d5 100%);
  background: -webkit-linear-gradient(90deg, #12c9fe 0%, #0e70d5 100%);
  background: linear-gradient(90deg, #12c9fe 0%, #0e70d5 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="@brand-color", endColorstr="@gradient-dark", GradientType=1);
  content: '';
  position: absolute;
  height: 100%;
  left: 0;
  -moz-opacity: 0.9;
  -khtml-opacity: 0.9;
  -webkit-opacity: 0.9;
  opacity: 0.9;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=90);
  filter: alpha(opacity=90);
  top: 0;
  width: 100%;
  z-index: -2;
}
#banner-area:before {
  background-color: #000;
  content: '';
  position: absolute;
  height: 100%;
  left: 0;
  -moz-opacity: 0.2;
  -khtml-opacity: 0.2;
  -webkit-opacity: 0.2;
  opacity: 0.2;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=20);
  filter: alpha(opacity=20);
  top: 0;
  width: 100%;
  z-index: -1;
}
#banner-area .banner-content-wrap {
  position: relative;
}
#banner-area .banner-content-wrap h1 {
  font-weight: 300;
  color: #fff;
}
#banner-area .banner-content-wrap h1 span {
  font-weight: 700;
}
#banner-area .banner-content-wrap .btn-brand {
  margin: 60px 0 90px;
}
